<template>
  <div class="projectSelect">
    <div class="pageMain">
      <van-cell-group class="contentBox">
        <van-cell :title="item.projectname" v-for="(item,index) in projectList" :key="index" @click="selectFn(item)">
            <template #label>
                <div>
                    <!-- 备注：项目状态 01未开工 02已开工 03已停工 04已竣工 -->
                    <van-tag v-if="item.state == '02'" type="primary" size="large" color="#29A7E2">已开工</van-tag>
                    <van-tag v-else-if="item.state == '04'" type="primary" size="large" color="#53BAC1">已完工</van-tag>
                    <van-tag v-else-if="item.state == '03'" type="primary" size="large" color="#D9544F">已停工</van-tag>
                    <van-tag v-else-if="item.state == '01'" type="primary" size="large" color="#CCCCCC">未开工</van-tag>
                </div>
            </template>
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectSelect",
  data() {
    return {
        projectList:[]
    };
  },
  methods: {
    getProjectListByEmployeeId() {
      this.$http.jsb.getProjectListByEmployeeId().then((res) => {
        if(res.code == 200){
          this.projectList = res.data
        }
      });
    },
    selectFn(item){
        localStorage.setItem('project',JSON.stringify(item))
        this.$router.back()
    }
  },
  mounted() {
    this.getProjectListByEmployeeId();
  },
};
</script>

